@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
*{
    margin:0;
    padding: 0;
    box-sizing: border-box;
}

.header{
    display: flex;
    justify-content: space-between;
    height: 70px;
    background-color:rgb(32, 61, 187);
    align-items: center;
    text-align: center;
    padding: 5px 20px;
}
.left{
    padding-left: 50px;
    display: flex;
    margin-top: -8px;
}
.left p{
    padding-right: 20px;
    font-size: 18px;

}
.right{
    display: flex;
    align-items: center;
}
.right select{
    margin-right: 20px;
    padding:10px;
    border: none;
    background-color: transparent;
    outline: none;
    cursor: pointer;
    color: white;
    font-weight: bold;
}
.right span{
    margin: 0 15px;
    cursor: pointer;
    color:white;
    font-weight: bold;
}
.right form{
    display: flex;
    border: 1px solid gray;
    align-items: center;
    padding: 0 5px;
    border-radius: 4px;
    background-color: rgb(194, 211, 209);
}
.right form input{
    border: none;
    background-color: transparent;
    padding: 5px ;
    outline: none;
}
.forms input{
 margin:10px;
 margin-left: 0;
 padding: 5px;
 outline: none;
 border-radius: 4px;
 border: 1px solid gray;
 width: 220px;

 
}
.forms label{
    padding: 0 10px;
    font-weight: bolder;
    font-family: sans-serif;
    font-size: 18px;

}
.forms button{
    margin:10px;
    padding:5px 8px;
    border: none;
    width: 150px;
    height: 40px;
    background-color: rgb(67, 48, 139);
    border-radius: 4px;
    color: white;
}
.forms button:hover{
    background-color: orangered;
}
.banner{
    height: 450px;
    background-image: url(/Finance-uzmap/static/media/moliyaa.a15adb62.jpg);
    background-size: cover;
    background-repeat: no-repeat;
}
.banner h1{
    color: white;
    font-size: 45px;
    font-family: 'Roboto', sans-serif;
    text-transform: capitalize;
    padding: 30px ;
    margin-left: 37px;
    font-weight: bold;

}
.banner .finInfos{
    display: flex;
    justify-content: space-around;
    color: white;
    font-weight: bold;
    margin-left: 20px;
    margin-top: 30px;
    font-family: 'Roboto', sans-serif;


}
.banner .finInfos .daromad,.defitsit,.xarajatlar{
    margin-right: 170px;
}
.banner .finInfos .infos_name{
    font-size: 40px;
    line-height: 10px;
}
.navbar{
    height: 70px;
    padding: 10px 30px;
    display: flex;
}
.navbar img{
    height: 50px;
    margin-left: 40px;
}
.infos{
    /* padding-left: 10px; */
    /* display: flex; */
    background-color: whitesmoke;
}
 .map_chart{
     margin-top: 5px;
    display: flex;
    justify-content: space-between;
    padding-left: 55px;
    /* border: 1px solid gray; */
    box-shadow: 2px 2px 2px 1px rgba(136, 189, 143, 0.1);
    background-color: rgb(243, 235, 235);

}
.infos .charts{
  display: flex;
  margin-top: 5px;
  box-shadow: 1px 1px 1px 1px rgba(81, 90, 99, 0.1);
  background-color: rgb(240, 231, 231);
}
.mapInfos{
    width: 500px;
    /* align-items: center;
